html {
  box-sizing: border-box;
  font-size: 16px; }
  @media (max-width: 640px) {
    html {
      font-size: 11px; } }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center; }

.main {
  margin: 0 auto;
  border-radius: 30px;
  background-color: #72BBFF;
  background-image: url("../assets/img/wind.jpg");
  color: #FFFFFF;
  font-size: 40px; }
  @media (max-width: 640px) {
    .main {
      margin: 30px; } }

.current {
  margin-top: 20px;
  text-align: center;
  font-size: 6.25rem;
  text-shadow: black 0.1em 0.1em 0.2em; }
  .current__location {
    text-align: center;
    font-size: 30px; }
  .current__days {
    display: grid;
    padding: 40px;
    grid-template-columns: 1fr 1fr;
    column-gap: 1rem;
    margin: 0 auto;
    text-align: center; }
    .current__days--today, .current__days--tomorrow {
      text-shadow: none;
      margin: 0 auto;
      font-size: 20px;
      padding-top: 8px;
      border-radius: 52px;
      width: 180px;
      height: 42px;
      background-color: #f5f037;
      color: #464646; }
      @media (max-width: 640px) {
        .current__days--today, .current__days--tomorrow {
          font-size: 15px;
          height: 30px;
          width: 100px; } }
    .current__days--hamburger {
      border: none;
      position: absolute;
      top: 16px;
      right: 16px;
      font-size: 15px;
      padding-top: 8px;
      text-align: center;
      border-radius: 52px;
      width: 62px;
      height: 30px;
      background-color: #f5f037;
      color: #464646; }
      @media (min-width: 640px) {
        .current__days--hamburger {
          display: none; } }

.hamburger {
  position: absolute;
  margin: 1rem 0 0 1rem;
  font-size: 15px;
  padding-top: 14px;
  text-align: center;
  border-radius: 10px;
  width: 45px;
  height: 45px;
  background-color: #69FF34;
  color: #464646; }
  @media (min-width: 640px) {
    .hamburger {
      display: none; } }

.settings {
  padding-top: 14px;
  background-color: #32bea6;
  display: block;
  position: relative;
  left: 42rem;
  top: 1rem;
  background-image: url("../assets/img/settings.svg");
  font-size: 15px;
  text-align: center;
  border-radius: 10px;
  width: 45px;
  height: 45px;
  color: #464646; }
  @media (max-width: 640px) {
    .settings {
      left: 23rem;
      top: 1rem; } }

.cities {
  display: grid;
  padding: 60px 40px 40px 40px;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 64px;
  row-gap: 36px;
  margin: 0 auto;
  text-align: center; }
  @media (max-width: 640px) {
    .cities {
      display: none;
      margin: 50px; } }
  @media (max-width: 640px) {
    .cities--visible {
      display: block;
      position: absolute;
      background-color: #FFFFFF;
      border-radius: 20px;
      border: solid 1px #464646; } }
  .cities__item {
    margin: 0 auto;
    font-size: 20px;
    padding-top: 8px;
    border-radius: 52px;
    width: 180px;
    height: 42px;
    background-color: #69FF34;
    color: #FFFFFF; }
    .cities__item--search {
      margin: 0 auto;
      width: 180px;
      position: relative;
      display: flex; }
    .cities__item--input {
      margin: 0 auto;
      font-size: 20px;
      border-radius: 52px;
      width: 180px;
      height: 42px;
      color: black; }

.searchButton {
  font-size: 20px;
  margin-left: -30px;
  border-bottom-right-radius: 26px;
  border-top-right-radius: 26px; }

.info {
  display: grid;
  padding: 40px;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 64px;
  row-gap: 36px;
  margin: 0 auto;
  text-align: center; }
  @media (max-width: 640px) {
    .info {
      grid-template-columns: 1fr 1fr;
      column-gap: 4rem;
      row-gap: 4rem;
      padding: 1rem 1rem 2rem 1rem; } }
  .info__item {
    color: #464646;
    font-weight: bold;
    background-color: #FFFFFF;
    width: 11.25rem;
    height: 102px;
    padding: 8px;
    font-size: 1rem;
    border-radius: 10px;
    border: 1px solid #464646; }
    .info__item--value {
      margin-top: -2px;
      font-size: 1.75rem; }
    .info__item--container {
      display: flex; }

/* The Modal (background) */
.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */ }

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  /* Could be more or less, depending on screen size */ }

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold; }

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer; }
